import React from 'react';
import Carrosel from './Carrousel';
import TimeUntil from './TimeUntil';
const Featured = () => {
    return (
        <div style={{ position: 'relative' }}>
            <Carrosel />

            <div className="artist_name">
                <div className="wrapper">
                    Ariana Gramde
                </div>
            </div>
            <TimeUntil />
        </div>
    );
};

export default Featured;